@import "./fonts.scss";

$grey1: #333333;
$pink: #f6f0e1;
$white: #ffffff;
$yellow: #f7ca59;
$blue: #a6d7c9;
$bloomarin: #bae8db;

body {
  margin: 0;
}

.container-wrap {
  max-width: 1150px;
  margin: 0 auto;
}

.section_title {
  font: normal 58px Playfair Display;
  line-height: 72px;
  margin-top: 130px;
  text-align: center;
  text-transform: capitalize;
  color: $grey1;

  @media screen and (min-width: 424px) and (max-width: 992px) {
    font-size: 30.8px;
    line-height: 45px;
  }

  @media screen and (min-width: 320px) and (max-width: 424px) {
    font-size: 26.4px;
  }
}

// first section
.welcome {
  position: relative;
  padding-top: 130px;
  background: $pink;
  padding-bottom: 108px;

  .block-wrap {
    justify-content: space-between;

    @media screen and (min-width: 320px) and (max-width: 1145px) {
      justify-content: center;
    }
  }

  .welcome-service {
    overflow: hidden;
    padding-top: 30px;
  }

  h1 {
    font: 60px Playfair Display;
    line-height: 120%;
    text-align: center;
    color: $grey1;
    margin: 0;

    @media (max-width: 768px) {
      font-size: 30.8px;
    }
  }

  p {
    font: normal 20px niveau-grotesk;
    line-height: 140%;
    text-align: center;
    letter-spacing: 0.3px;
    margin-top: 40px;
    color: $grey1;

    @media screen and (max-width: 768px) {
      font-size: 12px;
      margin: 20px auto 0;
    }
  }

  .block-wrap {
    position: relative;
    height: auto;
    margin: 33px auto 0;
    z-index: 0;

    .block-content {
      .block-about {
        margin-left: 52px;
        width: 1055px;
        height: 607px;
        background: $white;
        border: 4px solid $grey1;
        box-sizing: border-box;
        border-radius: 20px;
        z-index: 1;

        @media screen and (min-width: 992px) and (max-width: 1145px) {
          width: 1000px;
        }

        @media screen and (min-width: 550px) and (max-width: 1200px) {
          height: auto;
          width: 80%;
          margin: 0 auto;
        }

        @media screen and (min-width: 320px) and (max-width: 550px) {
          height: auto;
          width: 99%;
          margin: 0 auto;
        }

        .block-about_body {
          margin: 0 32px;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            margin: 0 2px;
          }

          .schema_inner {
            padding-top: 60px;

            @media screen and (min-width: 320px) and (max-width: 400px) {
              padding: 40px 2px 0;
            }

            .schema_screens {
              justify-content: space-evenly;
              position: relative;
              &::after {
                content: "";
                position: absolute;
                width: 4px;
                height: 277px;
                top: -60px;
                background-color: $grey1;
                @media screen and (min-width: 320px) and (max-width: 400px) {
                  height: 169px;
                  top: -40px;
                }
              }

              .schema_body {
                .schema_title {
                  font: 900 16px niveau-grotesk;
                  line-height: 24px;
                  display: flex;
                  align-items: center;
                  text-align: center;
                  letter-spacing: 0.3px;
                  color: $grey1;
                  margin-top: 19px;
                  @media screen and (max-width: 768px) {
                    font-size: 14.04px;
                    margin-top: 5px;
                  }
                }
              }

              .arrow {
                padding-top: 6px;
                z-index: 2;

                @media screen and (min-width: 320px) and (max-width: 768px) {
                  display: flex;
                  justify-content: center;
                  align-items: baseline;
                  width: 50%;
                }
              }

              svg {
                @media screen and (min-width: 320px) and (max-width: 768px) {
                  width: 50%;
                  height: auto;
                }
              }
            }

            .schema_cards {
              position: relative;
              z-index: 2;
              margin-top: 45px;
              justify-content: space-between;
              @media screen and (min-width: 768px) and (max-width: 1200px) {
                justify-content: space-around;
              }
              @media screen and (min-width: 520px) and (max-width: 768px) {
                justify-content: space-between;
              }
              @media screen and (min-width: 320px) and (max-width: 520px) {
                margin-top: 19px;
                justify-content: space-between;
              }

              .schema_card {
                position: relative;
                width: 144px;
                height: 176px;
                background: $yellow;
                border-radius: 10px;

                @media screen and (min-width: 768px) and (max-width: 1200px) {
                  width: 130px;
                  height: 163px;
                }

                @media screen and (min-width: 550px) and (max-width: 768px) {
                  width: 115px;
                  height: 105px;
                }

                @media screen and (min-width: 320px) and (max-width: 550px) {
                  width: 91px;
                  height: 81px;
                }

                &:nth-child(2),
                &:nth-child(3) {
                  &:before {
                    content: "";
                    position: absolute;
                    width: 50px;
                    height: 4px;
                    left: -48px;
                    background-color: #fcbd3b;
                    z-index: -2;

                    @media screen and (min-width: 550px) and (max-width: 1200px) {
                      width: 124px;
                      height: 4px;
                      left: -119px;
                    }

                    @media screen and (min-width: 400px) and (max-width: 550px) {
                      width: 110px;
                      height: 4px;
                      left: -110px;
                    }

                    @media screen and (min-width: 320px) and (max-width: 400px) {
                      width: 60px;
                      height: 4px;
                      left: -60px;
                    }
                  }
                }

                &:after {
                  content: " ";
                  position: absolute;
                  width: 144px;
                  height: 176px;
                  left: 8px;
                  top: 9px;
                  z-index: -1;
                  background: $grey1;
                  border-radius: 10px;

                  @media screen and (min-width: 768px) and (max-width: 1200px) {
                    width: 130px;
                    height: 163px;
                  }
                  @media screen and (min-width: 550px) and (max-width: 768px) {
                    width: 115px;
                    height: 105px;
                  }
                  @media screen and (min-width: 401px) and (max-width: 550px) {
                    width: 82px;
                    height: 81px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 401px) {
                    width: 0;
                    height: 0;
                  }
                }

                &:nth-child(2) {
                  width: 215px;
                  height: 176px;

                  @media screen and (min-width: 768px) and (max-width: 1200px) {
                    height: 163px;
                    width: 200px;
                  }
                  @media screen and (min-width: 550px) and (max-width: 768px) {
                    width: 115px;
                    height: 105px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 550px) {
                    width: 91px;
                    height: 81px;
                  }

                  &:after {
                    width: 215px;
                    height: 176px;

                    @media screen and (min-width: 768px) and (max-width: 1200px) {
                      height: 163px;
                      width: 200px;
                    }
                    @media screen and (min-width: 550px) and (max-width: 768px) {
                      width: 115px;
                      height: 105px;
                    }
                    @media screen and (min-width: 401px) and (max-width: 550px) {
                      width: 82px;
                      height: 81px;
                    }
                    @media screen and (min-width: 320px) and (max-width: 401px) {
                      width: 0;
                      height: 0;
                    }
                  }
                }

                .schema_title {
                  font: 900 13px niveau-grotesk;
                  line-height: 20px;
                  margin-top: 16px;
                  display: flex;
                  align-items: center;
                  text-align: center;
                  letter-spacing: 0.3px;
                  color: $grey1;
                  @media screen and (max-width: 768px) {
                    font-size: 12px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 768px) {
                    margin-top: 10px;
                  }
                }

                svg {
                  height: 77px;
                  @media screen and (min-width: 768px) and (max-width: 1024px) {
                    max-height: 65px;
                  }
                  @media screen and (min-width: 320px) and (max-width: 768px) {
                    width: 45%;
                    height: auto;
                    max-height: 35px;
                  }
                }
              }
            }
            .schema_text {
              font: bold 20px niveau-grotesk;
              line-height: 30px;
              text-align: center;
              letter-spacing: 0.3px;
              text-transform: uppercase;
              color: $grey1;
              margin-top: 40px;
            }
          }

          .list_inner {
            position: relative;
            padding-left: 70px;
            @media screen and (min-width: 400px) and (max-width: 1200px) {
              padding-left: 40px;
            }
            @media screen and (min-width: 320px) and (max-width: 400px) {
              padding-left: 4px;
            }
            h3 {
              padding-top: 36px;
              font: 900 34px niveau-grotesk;
              line-height: 48px;
              color: $grey1;

              @media (max-width: 960px) {
                font-size: 22px;
              }
            }
            .list {
              margin-top: 32px;
              padding-left: 25px;
              @media screen and (min-width: 320px) and (max-width: 550px) {
                margin-top: 0px;
              }
              .list_item {
                list-style: none;
                margin-bottom: 22px;
                @media screen and (min-width: 320px) and (max-width: 550px) {
                  margin-bottom: 5px;
                }
                position: relative;
                &:nth-child(2),
                &:nth-child(3) {
                  &:after {
                    content: "";
                    position: absolute;
                    top: 50%;
                    left: -25px;
                    transform: translateY(-50%);
                    width: 20px;
                    height: 3px;
                    background-color: #000000;
                  }
                }
                .list_img {
                  margin-right: 13px;
                }
                .list_title {
                  font: normal 22px niveau-grotesk;
                  line-height: 32px;
                  letter-spacing: 0.3px;
                  color: $grey1;

                  @media (max-width: 768px) {
                    font-size: 14.04px;
                  }
                }
              }
            }
            .list_line {
              position: absolute;
              top: 146px;
              bottom: 12%;
              left: 16.5%;
              width: 20px;
              border-radius: 7px 0 0 7px;
              border-left: 3px solid #000;
              border-top: 3px solid #000;
              border-bottom: 3px solid #000;

              @media screen and (min-width: 1067px) and (max-width: 1200px) {
                left: 39px;
                top: 131px;
                bottom: 10%;
              }
              @media screen and (min-width: 901px) and (max-width: 1067px) {
                left: 39px;
                top: 131px;
                bottom: 14%;
              }
              @media screen and (min-width: 814px) and (max-width: 901px) {
                left: 39px;
                top: 131px;
                bottom: 11%;
              }
              @media screen and (min-width: 769px) and (max-width: 814px) {
                left: 39px;
                top: 145px;
                bottom: 11%;
              }
              @media screen and (min-width: 550px) and (max-width: 769px) {
                left: 39px;
                top: 130px;
                bottom: 10.5%;
              }
              @media screen and (min-width: 446px) and (max-width: 550px) {
                left: 39px;
                top: 104px;
                bottom: 13%;
              }
              @media screen and (min-width: 414px) and (max-width: 446px) {
                left: 39px;
                top: 106px;
                bottom: 13%;
              }
              @media screen and (min-width: 400px) and (max-width: 414px) {
                left: 39px;
                top: 123px;
                bottom: 12%;
              }
              @media screen and (min-width: 336px) and (max-width: 400px) {
                left: 3px;
                top: 124px;
                bottom: 13%;
              }
              @media screen and (min-width: 320px) and (max-width: 335px) {
                left: 3px;
                top: 122px;
                bottom: 13%;
              }
            }
          }
        }

        &:after {
          content: " ";
          position: absolute;
          right: 0;
          top: 16px;
          z-index: -1;
          width: 1031px;
          height: 607px;
          background: $grey1;
          border-radius: 20px;
          @media screen and (max-width: 1200px) {
            width: 831px;
            right: 14%;
            height: 526px;
          }
          @media screen and (max-width: 1199px) {
            width: 831px;
            right: 14%;
            height: 100%;
          }

          @media screen and (min-width: 550px) and (max-width: 1190px) {
            height: 100%;
            width: 77%;
            margin: 0 auto;
            right: 8%;
          }
          @media screen and (min-width: 320px) and (max-width: 550px) {
            height: 100%;
            width: 99%;
            margin: 0 auto;
            right: -2%;
          }
        }
      }
    }
    .block-marker {
      position: absolute;
      width: 70px;
      height: 244px;
      background: $grey1;
      border-radius: 20px 0px 0px 20px;
      left: 0;
      top: 0;
      z-index: -1;

      @media screen and (min-width: 768px) and (max-width: 1200px) {
        left: 5%;
      }
      @media screen and (min-width: 550px) and (max-width: 768px) {
        left: 3%;
      }
      @media screen and (min-width: 320px) and (max-width: 550px) {
        left: 15%;
      }
      p {
        color: $white;
        white-space: nowrap;
        font: 900 14px niveau-grotesk;
        line-height: 143%;
        display: flex;
        align-items: center;
        text-align: center;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        margin: 0;
        transform: rotate(-90deg) translateY(-5px);
      }
    }
  }
}

.service {
  position: relative;
  background: $blue;
  @media screen and (min-width: 320px) and (max-width: 864px) {
    max-height: 800px;
    padding-bottom: 50px;
  }
  .stairs {
    position: absolute;
    width: 100%;
    top: 3px;
    .stairs_item {
      display: block;
      background-color: $pink;
      margin-bottom: 6px;
    }
    .stairs_item-two {
      height: 2px;
    }
    .stairs_item-four {
      height: 4px;
    }
    .stairs_item-eight {
      height: 8px;
    }
    .stairs_item:last-child {
      margin-bottom: 0;
    }
  }
  h3 {
    font: 900 32px niveau-grotesk;
    line-height: 48px;
    color: $grey1;
    padding-top: 100px;
    margin-bottom: 48px;
    @media screen and (min-width: 320px) and (max-width: 992px) {
      padding-top: 80px;
      margin: 0 0 10px;
      font-size: 20.4px;
    }
  }

  .services_body {
    justify-content: space-between;
    @media screen and (min-width: 320px) and (max-width: 1180px) {
      justify-content: space-evenly;
    }

    .service_card {
      margin: 0 5px;

      @media screen and (min-width: 428px) and (max-width: 622px) {
        margin: 0px 10px;
      }
      @media screen and (min-width: 320px) and (max-width: 428px) {
        margin: 0px 2px;
      }
      cursor: pointer;
      .serviceItem {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        background: #bae8db;
        border-radius: 10px;
        width: 256px;
        height: 270px;
        z-index: 2;
        margin-bottom: 80px;
        @media screen and (min-width: 992px) and (max-width: 1070px) {
          width: 205px;
          height: 335px;
        }
        @media screen and (min-width: 863px) and (max-width: 992px) {
          width: 205px;
          height: 230px;
        }
        @media screen and (min-width: 598px) and (max-width: 863px) {
          width: 279px;
          height: 225px;

          br {
            display: none;
          }
        }
        @media screen and (min-width: 466px) and (max-width: 598px) {
          width: 213px;
          height: 225px;

          br {
            display: none;
          }
        }
        @media screen and (min-width: 320px) and (max-width: 466px) {
          width: 145px;
          height: 261px;

          br {
            display: none;
          }
        }

        &:hover {
          &::after {
            height: 100%;
            background: $yellow;
          }
        }

        .serviceItem_code {
          position: absolute;
          top: 32px;
          left: 32px;
          width: 58px;
          height: 32px;
          background: #ffffff;

          border-radius: 36px;
          @media screen and (min-width: 864px) and (max-width: 1070px) {
            left: 12px;
          }
          @media screen and (min-width: 320px) and (max-width: 864px) {
            width: 44px;
            height: 26px;
            left: 12px;
          }

          span {
            font: 300 20px Fira Code;
            line-height: 32px;
            text-align: center;
            color: #888888;

            @media screen and (min-width: 320px) and (max-width: 992px) {
              font-size: 14.04px;
            }
          }
        }

        a {
          text-decoration: none;
        }

        .serviceItem_title {
          font: 900 20px niveau-grotesk;
          line-height: 28px;
          letter-spacing: 0.3px;
          text-transform: uppercase;
          color: $grey1;
          position: absolute;
          left: 32px;
          top: 90px;
          width: 75%;
          @media screen and (min-width: 865px) and (max-width: 1070px) {
            left: 12px;
          }
          @media screen and (min-width: 320px) and (max-width: 864px) {
            left: 12px;
            width: 90%;
          }
          @media (max-width: 992px) {
            font-size: 14.04px;
            line-height: 22px;
          }
        }
      }
    }
  }
}

.implement {
  position: relative;

  .implement_body {
    position: relative;
    margin-top: 100px;
    @media screen and (min-width: 320px) and (max-width: 767px) {
      margin-top: 20px;
    }

    h2 {
      font: 400 58px Playfair Display;
      color: $grey1;
      margin-bottom: 56px;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        padding: 10px;
        margin: 0 auto 10px;
        line-height: 1.25em;
        font-size: 30.8px;
        text-align: center;
      }

      br {
        @media screen and (min-width: 320px) and (max-width: 767px) {
          display: none;
        }
      }
    }

    .caseMenu {
      padding-left: 0;
      margin: 30px auto 0;

      @media screen and (min-width: 320px) and (max-width: 767px) {
        margin: 30px auto 45px;
      }
      .caseMenu_item {
        position: relative;
        width: 352px;
        height: 91px;
        list-style: none;
        border-bottom: 2px solid #d6d6d6;
        @media screen and (min-width: 320px) and (max-width: 767px) {
          width: 80%;
          height: 69px;
          margin: 0 auto;
        }

        &:last-child {
          margin-bottom: 0;
          border-bottom: none;
        }

        .linkAnimate {
          font: normal 24px niveau-grotesk;
          line-height: 30px;
          letter-spacing: 0.3px;
          color: #333333;
          text-decoration: none;

          @media (max-width: 767px) {
            font-size: 14.04px;
            line-height: 20px;
          }
        }
      }
      .caseMenu_arrow {
        display: none;
        position: absolute;
        right: -100px;

        @media screen and (min-width: 320px) and (max-width: 1155px) {
          right: 0;
        }
      }

      .active {
        border-bottom: 2px solid #fc6839;
        border-top: 2px solid #fc6839;
        .linkAnimate {
          font-weight: bold;
        }

        .caseMenu_arrow {
          display: block;
        }
      }
    }
    .integration_slider {
      background: $pink;
      border-radius: 20px;
      height: auto;
      width: 576px;
      margin-top: 200px;
      position: relative;

      @media screen and (min-width: 1070px) and (max-width: 1290px) {
        margin-left: -50px;
        width: 550px;
      }

      @media screen and (min-width: 767px) and (max-width: 1070px) {
        margin-left: -3%;
        width: 100%;
      }
      @media screen and (min-width: 320px) and (max-width: 767px) {
        margin-left: 0%;
        width: 100%;
        margin-top: 10px;
      }

      .dots {
        position: absolute;
        z-index: -1;
        top: -15%;
        right: -15%;

        @media screen and (min-width: 1140px) and (max-width: 1385px) {
          right: -6%;
        }
        @media screen and (max-width: 1140px) {
          display: none;
        }
      }

      .integration_slide {
        position: relative;
        display: none;
        margin: 37px 0 0px;
        @media screen and (min-width: 320px) and (max-width: 767px) {
          margin: 20px auto 50px;
          width: 80%;
          height: auto;
          padding-bottom: 0px;
        }
        .block_technical {
          position: absolute;
          top: -50px;
          left: 55px;
          @media screen and (min-width: 767px) and (max-width: 992px) {
            left: 15px;
          }
          @media screen and (min-width: 320px) and (max-width: 767px) {
            left: 50%;
            transform: translateX(-50%) scale(0.7);
            top: -40px;
          }

          img {
            margin-right: 15px;

            @media screen and (min-width: 768px) and (max-width: 992px) {
              max-height: 85px;
            }
          }
        }

        h3 {
          padding: 95px 0 0 64px;
          font: 900 32px niveau-grotesk;
          line-height: 48px;
          text-transform: uppercase;
          color: $grey1;
          @media screen and (min-width: 767px) and (max-width: 992px) {
            padding-left: 30px;
          }
          @media screen and (min-width: 320px) and (max-width: 767px) {
            text-align: center;
            padding: 100px 0 0 0;
            line-height: 1.25em;
            font-size: 22.4px;
          }
        }
        .integration_slide__text {
          font: 420 20px niveau-grotesk;
          line-height: 30px;
          letter-spacing: 0.3px;
          color: #000000;
          padding: 0 64px;
          margin: 16px auto 20px;

          @media screen and (min-width: 767px) and (max-width: 992px) {
            padding-left: 30px;
          }
          @media screen and (min-width: 320px) and (max-width: 767px) {
            padding: 0px;
            line-height: 1.25em;
            font-size: 14.04px;
            margin-bottom: 30px;
          }
          &:last-child {
            margin-bottom: 64px;
          }
        }
      }

      .active {
        display: block;
      }

      .integration_slider__indicators {
        width: 100%;
        height: 80px;
        background: #fc6839;
        border-radius: 0px 0px 20px 20px;

        @media screen and (min-width: 320px) and (max-width: 767px) {
          height: 60px;
        }

