      @media screen and (min-width: 769px) and (max-width: 800px) {
        bottom: 16%;
      }

      @media screen and (max-width: 768px) {
        bottom: 14%;
      }
      @media screen and (min-width: 500px) and (max-width: 767px) {
        bottom: -27%;
        left: auto;
        right: 4%;
        z-index: 2;
        transform: scale(0.7);
      }
      @media screen and (min-width: 427px) and (max-width: 500px) {
        bottom: -23%;
        left: auto;
        right: 4%;
        z-index: 2;
        transform: scale(0.6);
      }
      @media screen and (min-width: 320px) and (max-width: 427px) {
        bottom: -17%;
        left: auto;
        right: 4%;
        z-index: 2;
        transform: scale(0.6);
      }

      img {
        position: absolute;
      }
      h5 {
        position: absolute;
        top: 34%;
        left: 33px;
        font: 900 24px niveau-grotesk;
        line-height: 24px;
        align-items: center;
        letter-spacing: 0.3px;
        color: $grey1;
        transform: rotate(-4deg);
      }
      h6 {
        position: absolute;
        bottom: 0;
        left: 40px;
        font: 400 16px niveau-grotesk;
        line-height: 20px;
        letter-spacing: 0.3px;
        color: $grey1;
        transform: rotate(-4deg);

        @media screen and (min-width: 320px) and (max-width:426px) {
          font-size: 20px;
        }
      }
    }
    .advice_item {
      margin-bottom: 72px;
      @media screen and (min-width: 767px) and (max-width: 992px) {
        width: 115%;
        transform: translateX(-50px);
        margin-bottom: 35px;
      }
      @media screen and (min-width: 400px) and (max-width: 767px) {
        margin-bottom: 0;
        padding-left: 40px;
        width: 100%;
      }
      @media screen and (min-width: 320px) and (max-width: 400px) {
        margin-bottom: 0;
        padding-left: 20px;
        width: 100%;
      }
      .advice_title {
        margin-bottom: 40px;
        align-items: center;
        @media screen and (min-width: 768px) and (max-width: 992px) {
          margin-bottom: 10px;
        }
        @media screen and (min-width: 320px) and (max-width: 768px) {
          margin: 15px 0 10px;
        }
        h4 {
          font: bold 20px niveau-grotesk;
          line-height: 30px;
          letter-spacing: 0.3px;
          text-transform: uppercase;
          margin-left: 32px;
          color: $grey1;
          @media screen and (min-width: 768px) and (max-width: 992px) {
            margin-left: 10px;
          }
        }
      }
      p {
        font: normal 16px niveau-grotesk;
        line-height: 24px;
        letter-spacing: 0.3px;
        color: $grey1;
        @media screen and (min-width: 320px) and (max-width: 768px) {
          font-size: 14.04px;
        }
      }
    }
  }
}

.questions {
  background-color: #f3dba2;
  h3 {
    padding-top: 161px;
    margin: 0 0 72px;

    @media screen and (min-width: 320px) and (max-width: 768px) {
      padding-top: 80px;
      margin: 0 0 40px;
      text-align: left;
    }
  }

  .card_body {
    width: 100%;
    padding-bottom: 130px;
    @media screen and (max-width: 768px) and(max-width: 991px) {
      padding-bottom: 100px;
    }
    @media screen and (max-width: 768px) {
      flex-direction: column;
      padding-bottom: 80px;
    }
    .card {
      width: 180px;
      height: 170px;
      background: $yellow;
      border-radius: 10px;

      @media screen and (min-width: 768px) and (max-width: 1024px) {
        width: 140px;
        height: 130px;
      }
      @media screen and (min-width: 320px) and (max-width: 768px) {
        margin: 0 auto;
        width: 254px;
        height: 85px;
      }

      h5 {
        font: 900 13px niveau-grotesk;
        line-height: 20px;
        letter-spacing: 0.3px;
        text-transform: uppercase;
        color: $grey1;
        margin-left: 23px;

        @media screen and (min-width: 700px) and (max-width: 1024px) {
          margin-left: 15px;
        }

        br {
          @media screen and (min-width: 320px) and (max-width: 768px) {
            display: none;
          }
        }
      }
    }

    img {
      margin: 0 24px;
      @media screen and (min-width: 768px) and (max-width: 1024px) {
        margin: 0 5px;
      }
      @media screen and (max-width: 768px) {
        margin: 17px 24px;
        transform: rotate(90deg);
        width: 11%;
        max-width: 35px;
      }
    }
  }
}

.services {
  background: #f3dba2;
  padding-bottom: 160px;

  h3 {
    font: 900 32px niveau-grotesk;
    line-height: 48px;
    text-transform: uppercase;
    color: $grey1;
    padding-top: 160px;
    margin-bottom: 48px;
    @media screen and (min-width: 320px) and (max-width: 768px) {
      padding-top: 20px;
      margin: 0 0 10px;
      font-size: 20.4px;
    }
  }

  .services_body {
    justify-content: center;
    @media screen and (min-width: 320px) and (max-width: 1180px) {
      justify-content: space-evenly;
    }

    .service_card {
      &:nth-child(2),
      &:nth-child(5) {
        margin: 0 20px;
        @media screen and (min-width: 428px) and (max-width: 622px) {
          margin: 0px 10px;
        }
        @media screen and (min-width: 320px) and (max-width: 428px) {
          margin: 0px 2px;
        }
      }
      @media screen and (min-width: 428px) and (max-width: 622px) {
        margin: 0px 10px;
      }
      @media screen and (min-width: 320px) and (max-width: 428px) {
        margin: 0px 2px;
      }
      cursor: pointer;
      .serviceItem {
        position: relative;
        display: flex;
        flex-direction: column;
        justify-content: center;
        height: 290px;
        width: 352px;
        z-index: 2;
        @media screen and (min-width: 865px) and (max-width: 1095px) {
          width: 264px;
        }

        @media screen and (min-width: 768px) and (max-width: 865px) {
          width: 234px;
        }

        @media screen and (min-width: 428px) and (max-width: 768px) {
          width: 194px;
          height: 225px;
        }
        @media screen and (min-width: 320px) and (max-width: 428px) {
          height: 230px;
          width: 155px;
        }

        &:hover {
          &::after {
            height: 100%;
            background: #bae8db;
          }
          .btnEnter,
          .btnEnter_rotate {
            box-shadow: none;
          }
        }
        &::after {
          content: "";
          position: absolute;
          height: 80%;
          width: 100%;
          background: $yellow;
          border-radius: 10px;
          top: 50%;
          transform: translateY(-50%);
          left: 0;
          z-index: -1;
          transition: all 0.3s;
        }

        .serviceItem_code {
          position: absolute;
          top: 45px;
          left: 15px;
          width: 58px;
          height: 32px;
          background: #ffffff;
          border-radius: 36px;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            width: 44px;
            height: 26px;
          }

          span {
            font: 300 20px Fira Code;
            line-height: 32px;
            text-align: center;
            color: #888888;

            @media screen and (min-width: 320px) and (max-width: 768px) {
              font-size: 14.04px;
            }
          }
        }

        a {
          text-decoration: none;
        }

        .serviceItem_title {
          font: 900 20px niveau-grotesk;
          line-height: 28px;
          letter-spacing: 0.3px;
          text-transform: uppercase;
          color: $grey1;
          margin: 32px 0 46px 20px;
          @media screen and (min-width: 320px) and (max-width: 530px) {
            margin: 0px 0 0 8px;
          }

          @media (max-width: 780px) {
            font-size: 14.04px;
            line-height: 22px;
          }
        }

        .btnEnter {
          position: absolute;
          bottom: 70px;
          right: 35px;
          display: flex;
          width: 65px;
          background-color: #fff;
          border: 4px solid #333;
          border-radius: 10px;

          @media screen and (min-width: 320px) and (max-width: 768px) {
            bottom: 35px;
            right: 2px;
            transform: scale(0.8);
          }
        }

        .btnEnter_rotate {
          z-index: 1;
          transform: rotate(90deg) scale(1, -1);
          bottom: 4px;
          right: -12px;
          width: 47px;
          border: 4px solid #333;
          border-radius: 10px;
          display: flex;
        }

        .btnEnter,
        .btnEnter_rotate {
          height: 32px;
          box-shadow: 2px 2px 0 1px #333;
          padding: 9px 27px 9px 13px;
          transition: all 0.3s;
        }
        .btnEnter_icon,
        .btnEnter_rotate {
          position: absolute;
          background-color: #fff;
        }
        .btnEnter_icon {
          display: flex;
          align-items: center;
          z-index: 2;
          left: 8px;
          top: 0;
          width: 45px;
          height: 24px;
          border-radius: 10px;
        }
        .btnEnter_img {
          width: 23px;
        }
      }
    }
  }
}
